<div id='app'>
  <header class='status'>
    <div class='headerT'>
        <div class='headerIcon'>
            <i class='fas fa-plane'></i>
            <i class='fas fa-wifi'></i>
        </div>
        <div class='headerIcon'>
            <span class='h_time'>12:20</span>
        </div>
        <div class='headerIcon'>
            <i class='fas fa-sun'></i>
            <i class='fab fa-bluetooth-b'></i>
            <span class='h_battery'>80% <i class='fas fa-battery-three-quarters'></i></span>
        </div>
    </div>
    <div class='headerB'>
        <div class='headerIcon'>
            <span class='h_text'>{{menu}}</span>
        </div>
        <div class='headerIcon'>
            <span class='h_text'>{{title}}
                <span class='h_number'>{{num}}</span>
            </span>
        </div>
        <div class='headerIcon'>
            <i :class='icon'></i>
        </div>
    </div>
  </header>

    <router-view></router-view>

  <nav class='tab_Bar'>
    <router-link to='/friends' class='tab_BarTab'>
        <i class='fas fa-user'></i>
        <span class='tab_text'>Friends</span>
    </router-link>
    <router-link to='/chats' class='tab_BarTab'>
        <i class='fas fa-comment'></i>
        <span class='tab_text'>Chats</span>
    </router-link>
    <router-link to='/find' class='tab_BarTab'>
        <i class='fas fa-search'></i>
